.App {
  text-align: center;
    display: flex;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

:root{
    --main-color: #5558ca;
    --secondary-color: #faf;
    --white-background-color: #e7e7e7;
    --grey-line-color: #e7e7e7;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.text-right {
    text-align: right;
}

.text-left {
    text-align: left;
}
.text-center {
    text-align: center;
}

button {
    height: 30px;
    font-size: 15px;
}

.main-container {
    /*margin: 40px;*/
    /*margin-top: 80px;*/
}

.position-sticky {
    position: sticky;
    top: 150px;
}

.sticky-top-0 {
    position: sticky;
    top: 0px;
    background-color: #ffffff;
}


.selectViewBar > *  {
    border-right: 1px solid #aaa;
    padding: 5px;
    color: #000;
    text-decoration: none;
}

.selectViewBar > *:last-child {
    border-right: none;
}

.selectViewBar.white > * {
    color: #ffffff;
}

html, body, #root, .App {
    height: 100%;
}

.full-height {
    height: 100%;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}
