#superDataTable {
    border: none;
}

#superDataTableDiv {
    overflow-x: auto;
    font-family: "roboto", "serif";
    font-size: 12px;
    font-weight: 500;
    margin: 20px;
    border: 1px solid rgb(224,224,224);
    border-radius: 4px;

    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);

}

#superDataTable td, #superDataTable th  {
    padding: 4px 24px;
}

#superDataTable tr td {
    height:50px;
    border-bottom: 1px solid rgba(224, 224, 224, 1);
}

#superDataTable thead tr {
    text-transform: capitalize;
    font-weight: 900;
    background-color: #777777;
    color: #fff;
}

#superDataTable tr:nth-child(even) {
    height:50px;
    background-color: #fafafa;
}
