.modal-base {
    width: 100%;
    height: 100%;
    background-color: grey;
    position: fixed;
    z-index: 5;
    opacity: 1;
    -webkit-transition: opacity 1s linear 0.5s;
    transition: opacity 1s linear 0.5s;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.displayNone {
    display: none;
    opacity: 0;
}

