@media print {
    body * {
        visibility: hidden;
    }

    .boPaperToPrint > * {
        margin-top: -200px;
    }


    #bill_preview, #bill_preview *, .printable, .printable * {
        visibility: visible;
    }

    #bill_preview {
        position: absolute;
        width: 100%;
        /*padding: 30px;*/
        top: 30px;
        left: 0px;
        box-sizing: border-box;
    }

    .ticketBillDisplay tr:nth-child(13n+10){
        border-bottom: 1px solid black;
    }


    .not-suitable-to-print {
        background-color: #000000;
    }
    /*.ticket-tr {*/
    /*page-break-before: always;*/
    /*page-break-after:auto*/
    /*}*/
    /*table {*/
    /*page-break-inside:auto;*/
    /*}*/
    /*tr {*/
    /*page-break-inside:avoid;*/
    /*page-break-after:auto*/
    /*}*/

    @page {
        size: auto;
        margin-top: 1cm;
        padding: 10mm 30mm;
        border-bottom: 10px solid black;
    }

    @page:first {
        margin-top: 0px;
    }


    .page-break {
        page-break-inside: avoid;
    }

    .my-header {
        position: -webkit-sticky;
        position: sticky;
    }



    /*#bill_preview {display: inline-block}*/
}



