.modal-base {
    width: 100%;
    height: 100%;
    background-color: grey;
    position: fixed;
    z-index: 5;
    opacity: 1;
    transition: opacity 1s linear 0.5s;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.displayNone {
    display: none;
    opacity: 0;
}

.modal-body {
    height: 500px;
    background-color: white;
    padding: 30px 10px;
    border-radius: 5px;
    opacity: 1;
}

.scrollable {
    overflow: scroll;
}

.max-height-500 {
    max-height: 500px;
}

